<template>
    <el-container>
        <el-aside class="left-group-aside">
            <lefttype :typelist="logTypes" @callType="callbackType"></lefttype>
        </el-aside>
        <el-main>
            <toolbar :buttonList="buttonList" @callFunction="callFunction">
                <el-date-picker
                    v-model="pickDate"
                    type="week"
                    format="yyyy 第 WW 周"
                    placeholder="选择周"
                    :picker-options="pickerOptions"
                    @change="changeweek"
                    :clearable="false"
                >
                </el-date-picker>
            </toolbar>
            <!--列表-->
            <div class="block"></div>
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
                @sort-change="changeSort"
                @row-click="clickTable"
                ref="refTable"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline>
                            <el-form-item label="参数:">
                                <span>{{
                                    props.row.params
                                }}</span> </el-form-item
                            ><br />
                            <el-form-item label="操作内容:">
                                <span>{{ props.row.operation }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="type"
                    label="种类"
                    :sort-orders="['ascending', 'descending', null]"
                    sortable
                >
                </el-table-column>
                <el-table-column
                    prop="ip"
                    label="IP/HOST"
                    :sort-orders="['ascending', 'descending', null]"
                    sortable
                >
                </el-table-column>
                <el-table-column
                    prop="url"
                    label="访问地址"
                    :sort-orders="['ascending', 'descending', null]"
                    sortable
                >
                </el-table-column>
                <el-table-column
                    prop="params"
                    label="参数"
                    :show-overflow-tooltip="true"
                >
                </el-table-column>
                <el-table-column
                    prop="operation"
                    label="操作内容"
                    :sort-orders="['ascending', 'descending', null]"
                    sortable
                    :show-overflow-tooltip="true"
                    min-width="150"
                >
                </el-table-column>
                <el-table-column
                    prop="operator"
                    label="操作者"
                    :sort-orders="['ascending', 'descending', null]"
                    sortable
                >
                </el-table-column>
                <el-table-column
                    prop="operatedate"
                    label="日期"
                    :sort-orders="['ascending', 'descending', null]"
                    sortable
                >
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>
        </el-main>
    </el-container>
</template>
<script>
import { getoplogpagelist, getLogTypes } from "../../api/api";
import lefttype from "../../components/LogType";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import util from "../../../util/date";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { lefttype, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            logTypes: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            selectType: 999,
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                },
                firstDayOfWeek: 1,
            },
            pickDate: util.formatDate.format(util.getMonday(), "yyyy-MM-dd"),
            sort: "",
            orderField: "",
        };
    },
    methods: {
        clickTable(row, index, e) {
            this.$refs.refTable.toggleRowExpansion(row);
        },
        changeSort(val) {
            // column: {…} order: "ascending" prop: "date"
            // 根据当前排序重新获取后台数据,一般后台会需要一个排序的参数
            if (val.order == "ascending") {
                this.sort = "asc";
            } else if (val.order == "descending") {
                this.sort = "desc";
            }
            this.orderField = val.prop;

            this.getPageList();
        },
        changeweek(val) {
            this.pickDate = val;
            this.getlogtype();
            this.getPageList();
        },
        callbackType(item) {
            this.selectType = item.type;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList() {
            let paras = {
                type: this.selectType,
                startDate: this.pickDate,
                orderField: this.orderField,
                sort: this.sort,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getoplogpagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getPageList();
        },
        getlogtype() {
            getLogTypes({ startDate: this.pickDate }).then((res) => {
                if (res.success) {
                    this.logTypes = res.data;
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        this.getlogtype();

        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide

            this.buttonList.push(element);
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
/deep/ .el-table__expand-icon {
    visibility: hidden;
}
/deep/ .el-table__expanded-cell {
    background-color: #c8e1fd !important;
}
</style>


