<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>日志类型</span>
        </div>
        <div
            v-for="item in typelist"
            :key="item.type"
            :class="item.type == seltype ? 'active' : ''"
            class="group-item"
            @click="typeClick(item)"
        >
            <div>
                <i class="el-icon-s-grid"></i>
                {{ item.type }}&nbsp;({{ item.Count }})
            </div>
            <i
                class="el-icon-close"
                v-if="item.type == seltype"
                style="float: right"
                @click.stop="removeSelected"
            ></i>
        </div>
    </el-card>
</template>
<script>
export default {
    name: "lefttype",
    props: {
        typelist: {
            type: Array,
            required: true,
        },
    },
    data() {
        return {
            seltype: "",
        };
    },
    methods: {
        typeClick(item) {
            this.seltype = item.type;
            this.$emit("callType", item); //将值传给父组件
        },
        removeSelected() {
            this.seltype = 999;
            this.$emit("callType", {Coucnt:999,type:"全部"});
        },
    },
};
</script>
<style scoped>
.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}
.clearfix:after {
    clear: both;
}

.box-card .item.active {
    background: #ebf5ff;
}

.box-card .item:hover {
    background: #ebf5ff;
}
</style>


